import React from 'react'

export default class Color extends React.Component {
    
    state = {
        colors: ['#000000', '#ffffff', '#1fea00', '#ea0000', '#0f59e2']
    }
    render() {
        return (
            <div className="colors">
                {this.state.colors.map((c, i) => {
                    if (c === '#ffffff') {
                        return <div className="color" key={i} style={{'background': c, 'border': '1px solid black'}} onClick={() => this.props.changeColor(c)}></div>

                    }
                    else {
                        return <div className="color" key={i} style={{'background': c}} onClick={() => this.props.changeColor(c)}></div>
                    }
                })}
            </div>
        )
    }
}