import React from 'react'
import axios from 'axios'

export default class Canvas extends React.Component {
    state = {
        strokestyle: '#000000',
        lineWidth: 20,
        isPainting: false,
        prevPos: {offsetX: 0, offsetY: 0},
        mediaHandle: ''
    }

    handleInput= e => {
        this.setState({
            mediaHandle: e.target.value
        })
    }

    submit = () => {
        if (this.state.mediaHandle !== '') {
            let data = new FormData();
            this.canvas.toBlob((blob) => {
                data.append('uploadFile', blob, this.state.mediaHandle);
                data.append('mediaHandle', this.state.mediaHandle)
        
                axios
                    .post('upload.php', data, {
                        headers: {
                            'Content-Type': 'multipart/form-data',
                        },
                    })
                    .then(res => {
                        console.log(res)
                    });
            });
        } else {
            alert('Hoď nám svůj username')
        }
    }

    componentDidMount = () => {
        this.canvas.width = 1000
        this.canvas.height = 1500
        this.ctx = this.canvas.getContext('2d')
        this.ctx.lineJoin = 'round'
        this.ctx.lineCap = 'round'

        this.canvas.addEventListener("touchstart", e => {
           
            e.preventDefault()
            e.stopPropagation();
            let rect = e.target.getBoundingClientRect();
            let offsetX = e.targetTouches[0].pageX - rect.left
            let offsetY = e.targetTouches[0].pageY - rect.top

            offsetX *= 8
            offsetY *= 8
            this.setState({
                isPainting: true,
                prevPos: {offsetX, offsetY}
            })

            console.log("START")
            console.log(this.state.prevPos)
            
        })

        this.canvas.addEventListener("touchend", e => {
            

            
            e.preventDefault()
            e.stopPropagation()
            this.endPaintEvent()
            
        })

        this.canvas.addEventListener("touchmove", e => {
            e.preventDefault()
            e.stopPropagation();

            
            if (this.state.isPainting) {
                let rect = e.target.getBoundingClientRect()
                let offsetX = e.targetTouches[0].pageX - rect.left
                let offsetY = e.targetTouches[0].pageY - rect.top
                offsetX *= 8
                offsetY *= 8
                const offsetData = {offsetX, offsetY}
                this.paint(offsetData)
                console.log(offsetData)
            }

        })

        this.canvas.addEventListener("touchend", e => {
            this.endPaintEvent()
        })
    }

    onMouseDown = ({nativeEvent}) => {
        const {offsetX, offsetY} = nativeEvent
        this.setState({
            isPainting: true,
            prevPos: {offsetX, offsetY}
        })
    }

    onMouseMove = ({nativeEvent}) => {
        if (this.state.isPainting) {
            const {offsetX, offsetY} = nativeEvent
            const offsetData = {offsetX, offsetY}
            this.paint(offsetData)
            console.log(offsetX)
        }
        
    }

    endPaintEvent = () => {
        if (this.state.isPainting) {
            this.setState({isPainting: false})
        }
    }

    paint = (currPos) => {
        const {offsetX, offsetY} = currPos
        const {offsetX: x, offsetY: y} = this.state.prevPos
        console.log(currPos)
        this.ctx.beginPath()
        this.ctx.strokeStyle = this.props.strokeStyle
        this.ctx.lineWidth = this.props.lineWidth
        this.ctx.moveTo(x, y)
        this.ctx.lineTo(offsetX, offsetY)
        this.ctx.stroke()
        this.setState({
            prevPos: {offsetX, offsetY}
        })
    }

    render() {
        return (
            <div>
                    <canvas 
                        className="canvas"
                        ref={ref => this.canvas = ref}

                        onMouseDown={this.onMouseDown}
                        onMouseUp={this.endPaintEvent}
                        onMouseLeave={this.endPaintEvent}
                        onMouseMove={this.onMouseMove}

                    />
                    <div className="upload" >
                        <input className="upload__input" onChange={this.handleInput} value={this.state.mediaHandle} placeholder="Sem hoď svůj IG username nebo adresu tvojí mámy"></input>
                        <button className="upload__button" onClick={this.submit}>Poslat</button>
                    </div>
                        
                    </div>
                
        )
    }
}