import React from 'react'
import './style.css'
import Canvas from './components/Canvas.js'
import Color from './components/Color.js'



export default class App extends React.Component {
  state = {
    lineWidth: 20,
    strokeStyle: '#000000'
  }
  


  changeWidth = e => {
    this.setState({
      lineWidth: e.target.value
    })
  }

  changeColor = (color) => {
    this.setState({
      strokeStyle: color
    })
  }

  render() {
    return (
      <div className="container">
        <div className="shirt">
          <Canvas lineWidth={this.state.lineWidth} strokeStyle={this.state.strokeStyle}/>
        </div>
        
        <div className="tools">
          <div className="brushSize">
            <input type="range" min="0" max="200"  step="10" value={this.state.lineWidth} onChange={this.changeWidth}></input>
          </div>
          <Color changeColor={this.changeColor} />
        </div>
      </div>
    );
  }
}
